.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.leftSide:not(focused) {
  -webkit-animation: focused-left-rev 500ms ease-in-out;
          animation: focused-left-rev 500ms ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.rightSide:not(focused) {
  -webkit-animation: focused-right-rev 500ms ease-in-out;
          animation: focused-right-rev 500ms ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.leftSide.focused {
  -webkit-animation: focused-left 800ms cubic-bezier(0.67, -0.42, 0.58, 1);
          animation: focused-left 800ms cubic-bezier(0.67, -0.42, 0.58, 1);
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.rightSide.focused {
  -webkit-animation: focused-right 800ms cubic-bezier(0.67, -0.42, 0.58, 1);
          animation: focused-right 800ms cubic-bezier(0.67, -0.42, 0.58, 1);
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes focused-left {
  from { width: 50vw; }
  to { width: 10vw; }
}

@keyframes focused-left {
  from { width: 50vw; }
  to { width: 10vw; }
}

@-webkit-keyframes focused-right {
  from { width: 50vw; }
  to { width: 90vw; }
}

@keyframes focused-right {
  from { width: 50vw; }
  to { width: 90vw; }
}

@-webkit-keyframes focused-left-rev {
  from { width: 10vw; }
  to { width: 50vw; }
}

@keyframes focused-left-rev {
  from { width: 10vw; }
  to { width: 50vw; }
}

@-webkit-keyframes focused-right-rev {
  from { width: 90vw; }
  to { width: 50vw; }
}

@keyframes focused-right-rev {
  from { width: 90vw; }
  to { width: 50vw; }
}


