.Logo:not(vertical).focusJustChanged {
    -webkit-transform-origin: top left;
        -ms-transform-origin: top left;
            transform-origin: top left;
    -webkit-animation: spin-rev 500ms ease-in-out;
            animation: spin-rev 500ms ease-in-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.Logo.vertical.focusJustChanged {
    -webkit-transform-origin: top left;
        -ms-transform-origin: top left;
            transform-origin: top left;
    -webkit-animation: spin 800ms cubic-bezier(0.67, -0.42, 0.58, 1);
            animation: spin 800ms cubic-bezier(0.67, -0.42, 0.58, 1);
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
    }
}

@keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
    }
}

@-webkit-keyframes spin-rev {
    from {
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
    }
    to {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

@keyframes spin-rev {
    from {
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
    }
    to {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}


