a {
  color: #385196;
  text-decoration: none;
}

p a {
  font-weight: bold;
}

b {
  color: black;
  -webkit-transform: scale(1.5);
      -ms-transform: scale(1.5);
          transform: scale(1.5);
}

a:hover {
  text-decoration: underline;
}

.entering div.header {
  -webkit-animation: enter-down 800ms ease-in-out 0ms;
          animation: enter-down 800ms ease-in-out 0ms;
  -webkit-animation-fill-mode: forward;
          animation-fill-mode: forward;
}

.entering div.body {
  -webkit-animation: enter-up 1000ms ease-in-out 0ms;
          animation: enter-up 1000ms ease-in-out 0ms;
  -webkit-animation-fill-mode: forward;
          animation-fill-mode: forward;
}

@-webkit-keyframes enter-down {
  0% {
    -webkit-transform: translateY(-1000px);
            transform: translateY(-1000px);
  }
  10% {
    -webkit-transform: translateY(-1000px);
            transform: translateY(-1000px);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes enter-down {
  0% {
    -webkit-transform: translateY(-1000px);
            transform: translateY(-1000px);
  }
  10% {
    -webkit-transform: translateY(-1000px);
            transform: translateY(-1000px);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@-webkit-keyframes enter-up {
  0% {
    -webkit-transform: translateY(1000px);
            transform: translateY(1000px);
  }
  50% {
    -webkit-transform: translateY(1000px);
            transform: translateY(1000px);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes enter-up {
  0% {
    -webkit-transform: translateY(1000px);
            transform: translateY(1000px);
  }
  50% {
    -webkit-transform: translateY(1000px);
            transform: translateY(1000px);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}


