.menu-tab.selected:not(.focused) {
  border-bottom: 1px white solid;
}

.menu-tab.selected:not(.focused).focusedJustChanged {
  -webkit-animation: border-selected-rev 500ms ease-in-out;
          animation: border-selected-rev 500ms ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.menu-tab.selected.focused {
  -webkit-animation: border-selected 500ms ease-in-out;
          animation: border-selected 500ms ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.menu-tab:not(.selected):not(.focused).focusedJustChanged {
  -webkit-animation: border-not-selected-rev 500ms ease-in-out;
          animation: border-not-selected-rev 500ms ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.menu-tab:not(.selected).focused {
  -webkit-animation: border-not-selected 500ms ease-in-out;
          animation: border-not-selected 500ms ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes border-selected {
  30% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
    border-left: 0px white solid;
    width: 100%;
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    border-left: 2px white solid;
    width: 0px;
  }
}

@keyframes border-selected {
  30% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
    border-left: 0px white solid;
    width: 100%;
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    border-left: 2px white solid;
    width: 0px;
  }
}

@-webkit-keyframes border-selected-rev {
  from {
    border-left-width: 2px;
    border-bottom-width: 0px;
    width: 0%;
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  30% {
    width: 0%;
  }
  to {
    border-left-width: 0px;
    border-bottom: white solid;
    border-bottom-width: 1px;
    width: 90%;
  }
}

@keyframes border-selected-rev {
  from {
    border-left-width: 2px;
    border-bottom-width: 0px;
    width: 0%;
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  30% {
    width: 0%;
  }
  to {
    border-left-width: 0px;
    border-bottom: white solid;
    border-bottom-width: 1px;
    width: 90%;
  }
}

@-webkit-keyframes border-not-selected {
  30% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
    border-left: 0px white solid;
    width: 100%;
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    border-left: 2px rgba(255, 255, 255, 0.3) solid;
    width: 0px;
  }
}

@keyframes border-not-selected {
  30% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg);
    border-left: 0px white solid;
    width: 100%;
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    border-left: 2px rgba(255, 255, 255, 0.3) solid;
    width: 0px;
  }
}

@-webkit-keyframes border-not-selected-rev {
  from {
    border-left-width: 2px;
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  to {border-left-width: 0px;}
}

@keyframes border-not-selected-rev {
  from {
    border-left-width: 2px;
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  to {border-left-width: 0px;}
}

.menu-text:not(.focused) {
  -webkit-animation: squash-rev 500ms ease-in-out;
          animation: squash-rev 500ms ease-in-out;
  /* animation-fill-mode: forwards; */
}

.menu-text.focused {
  -webkit-animation: squash 500ms ease-in-out;
          animation: squash 500ms ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes squash {
  20% {-webkit-transform: scaleX(1.2);transform: scaleX(1.2);}
  100% {-webkit-transform: scaleX(0);transform: scaleX(0);}
}

@keyframes squash {
  20% {-webkit-transform: scaleX(1.2);transform: scaleX(1.2);}
  100% {-webkit-transform: scaleX(0);transform: scaleX(0);}
}

@-webkit-keyframes squash-rev {
  from {-webkit-transform: scaleX(0);transform: scaleX(0);}
  to {-webkit-transform: scaleX(1);transform: scaleX(1);}
}

@keyframes squash-rev {
  from {-webkit-transform: scaleX(0);transform: scaleX(0);}
  to {-webkit-transform: scaleX(1);transform: scaleX(1);}
}



