a.icon {
  color: white;
}

.ContactInfo.focusJustChanged > div:nth-child(1) {
  -webkit-animation: bounce-up1 400ms ease-in-out;
          animation: bounce-up1 400ms ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.ContactInfo.focusJustChanged > div:nth-child(2) {
  -webkit-animation: bounce-up2 400ms ease-in-out 300ms;
          animation: bounce-up2 400ms ease-in-out 300ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.ContactInfo.focusJustChanged > div:nth-child(3) {
  -webkit-animation: bounce-up3 400ms ease-in-out 300ms;
          animation: bounce-up3 400ms ease-in-out 300ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.ContactInfo:not(.focusJustChanged) > div:nth-child(1) {
  -webkit-animation: bounce-down1 400ms ease-in-out;
          animation: bounce-down1 400ms ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.ContactInfo:not(.focusJustChanged) > div:nth-child(2) {
  -webkit-animation: bounce-down2 400ms ease-in-out;
          animation: bounce-down2 400ms ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.ContactInfo:not(.focusJustChanged)>div:nth-child(3) {
  -webkit-animation: bounce-down3 400ms ease-in-out;
          animation: bounce-down3 400ms ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes bounce-up1 {
  0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  100% {
    -webkit-transform: translateY(-120px) scale(0.8);
            transform: translateY(-120px) scale(0.8);
  }
}

@keyframes bounce-up1 {
  0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  100% {
    -webkit-transform: translateY(-120px) scale(0.8);
            transform: translateY(-120px) scale(0.8);
  }
}

@-webkit-keyframes bounce-up2 {
  0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  100% {
    -webkit-transform: translateY(-60px) scale(0.8);
            transform: translateY(-60px) scale(0.8);
  }
}

@keyframes bounce-up2 {
  0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  100% {
    -webkit-transform: translateY(-60px) scale(0.8);
            transform: translateY(-60px) scale(0.8);
  }
}

@-webkit-keyframes bounce-up3 {
  0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  100% {
    -webkit-transform: translateY(0px) scale(0.8);
            transform: translateY(0px) scale(0.8);
  }
}

@keyframes bounce-up3 {
  0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  100% {
    -webkit-transform: translateY(0px) scale(0.8);
            transform: translateY(0px) scale(0.8);
  }
}

@-webkit-keyframes bounce-down1 {
  0% {
    -webkit-transform: translateY(-120px) scale(0.8);
            transform: translateY(-120px) scale(0.8);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes bounce-down1 {
  0% {
    -webkit-transform: translateY(-120px) scale(0.8);
            transform: translateY(-120px) scale(0.8);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@-webkit-keyframes bounce-down2 {
  0% {
    -webkit-transform: translateY(-60px) scale(0.8);
            transform: translateY(-60px) scale(0.8);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes bounce-down2 {
  0% {
    -webkit-transform: translateY(-60px) scale(0.8);
            transform: translateY(-60px) scale(0.8);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@-webkit-keyframes bounce-down3 {
  0% {
    -webkit-transform: translateY(0px) scale(0.8);
            transform: translateY(0px) scale(0.8);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes bounce-down3 {
  0% {
    -webkit-transform: translateY(0px) scale(0.8);
            transform: translateY(0px) scale(0.8);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}


